'use client';

import { useState } from 'react';
import { useLanguage } from '@/app/context/language-context';
import { LoginForm } from '@/components/auth/login-form';
import { RegisterForm } from '@/components/auth/register-form';
import { AuthLayout } from '@/components/auth/auth-layout';

export default function Home() {
  const [isLogin, setIsLogin] = useState(true);
  const { t } = useLanguage();

  return (
    <AuthLayout
      title={isLogin ? (t('auth.loginTitle') || 'Chào mừng trở lại!') : (t('auth.registerTitle') || 'Tạo tài khoản mới')}
      subtitle={isLogin ? (t('auth.loginSubtitle') || 'Vui lòng đăng nhập để tiếp tục.') : (t('auth.registerSubtitle') || 'Bắt đầu quản lý tài chính của bạn ngay hôm nay.')}
    >
      {isLogin ? (
        <LoginForm onToggle={() => setIsLogin(false)} />
      ) : (
        <RegisterForm onToggle={() => setIsLogin(true)} />
      )}
    </AuthLayout>
  );
}
