'use client';

import { Wallet } from 'lucide-react';

interface AuthLayoutProps {
    children: React.ReactNode;
    title: string;
    subtitle: string;
}

export function AuthLayout({ children, title, subtitle }: AuthLayoutProps) {
    return (
        <div className="min-h-screen w-full flex bg-background">
            {/* Left Column - Illustration */}
            <div className="hidden lg:flex w-1/2 bg-primary p-12 flex-col justify-between relative overflow-hidden">
                <div className="relative z-10">
                    <div className="flex items-center gap-3 mb-8">
                        <div className="h-10 w-10 rounded-xl bg-white/20 backdrop-blur flex items-center justify-center">
                            <Wallet className="h-6 w-6 text-white" />
                        </div>
                        <span className="text-2xl font-bold text-white">ExpenseAI</span>
                    </div>
                    <h1 className="text-4xl font-bold text-white mb-4">
                        Quản lý tài chính thông minh với AI
                    </h1>
                    <p className="text-blue-100 text-lg max-w-md">
                        Theo dõi chi tiêu, phân tích xu hướng và nhận gợi ý tiết kiệm từ trợ lý ảo thông minh của bạn.
                    </p>
                </div>

                {/* Abstract Shapes/Pattern */}
                <div className="absolute top-0 right-0 -mr-20 -mt-20 w-96 h-96 bg-white/10 rounded-full blur-3xl" />
                <div className="absolute bottom-0 left-0 -ml-20 -mb-20 w-80 h-80 bg-blue-400/20 rounded-full blur-3xl" />

                <div className="relative z-10 text-blue-100 text-sm">
                    © 2024 ExpenseAI. All rights reserved.
                </div>
            </div>

            {/* Right Column - Form */}
            <div className="w-full lg:w-1/2 flex items-center justify-center p-8">
                <div className="w-full max-w-md space-y-8">
                    <div className="text-center lg:text-left">
                        <h2 className="text-3xl font-bold tracking-tight text-foreground">{title}</h2>
                        <p className="mt-2 text-muted-foreground">{subtitle}</p>
                    </div>
                    {children}
                </div>
            </div>
        </div>
    );
}
